.section {
    margin: 10px 0;
}

.section__header {
    display: flex;
    flex-grow: 1;
    background-color: #ffffff;
    padding: 0 20px;
    margin-bottom: 2px;
}

.section__title {
    flex-grow: 1;
}

@media (max-width: 767px) {
    .section__header {
        flex-direction: column;
    }
}